<template>
    <div class="container-fluid">
        <div class="row text-center py-3 my-5">
            <h1 class="my-3">{{prop}}</h1>
        </div>
        <div class="row">
            <AppCart v-for="watch in watchData" :key="watch.filmId" :state="'default'" :type="watch.type" :kpid="watch.filmId" :title="watch.nameRu" :year="watch.year" />
        </div>
    </div>
</template>

<script>
import AppCart from '../components/AppCart.vue'
const axios = require('axios')
export default {
    name: 'Main',
    components: {
        AppCart
    },
    data: () => {
        return {
            prop: 'Главная',
            watchData: []
        }
    },
    methods: {
        
    },
    mounted() {
        document.title = 'Main'
        axios
            .get('https://iny.su/api/0.1/media/mediaMainList.json')
            .then(data => {
                this.watchData = data.data
            })

    }
}
</script>