<template>
    <div @click="go()" class="col-12 mb-3" :class="(state == 'default' ? 'col-md-6 col-lg-4 col-xl-3':'ps-md-5')">
        <div class="row justify-content-center align-items-center card-poster px-1 theme-duration" :class="(state == 'default' ? 'card-poster_view-default':'card-poster_view-max-content')" :style="bgImage()">
            <div class="col-12 text-center">
                <h5 class="white-text my-2">{{get_type()}}</h5>
                <h5 class="white-text my-2">{{title}}</h5>
                <h6 class="white-text my-2">{{year}}</h6>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'AppCart',
    data: () => {
        return {
            data: ''
        }
    },
    props: {
        state: String,
        type: String,
        kpid: Number,
        title: String,
        year: String
    },
    methods: {
        bgImage() {
            return `background-image: url("https://kinopoiskapiunofficial.tech/images/posters/kp/${this.kpid}.jpg");`
        },
        get_type() {
            switch(this.type) {
                case 'FILM':
                case 'UNKNOWN':
                    return 'фильм'
                case 'TV_SHOW':
                    return 'сериал'
            }
        },
        go() {
            this.$router.push('/watch?kpid=' + this.kpid)
        }
    }
}
</script>

<style scoped>

.card-border {
    border-color: white!important;
}
.card-poster {
    /* height: 70vh; */
    filter: grayscale(1);
    background-color: #242424aa;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-blend-mode: overlay;
    transition: background-color, filter;
    transition-duration: 0.5s;
}
.card-poster:hover {
    filter: grayscale(0);
    background-color: #00000066;
}

.card-poster_view-default {
    height: 50vh;
}
.card-poster_view-max-content {
    height: 30vh;
}
@media (min-width: 576px) {
    .card-poster_view-default {
        height: 50vh;
    }
    .card-poster_view-max-content {
        height: 30vh;
    }
}

@media (min-width: 768px) {
    .card-poster_view-default {
        height: 50vh;
    }
    .card-poster_view-max-content {
        height: 40vh;
    }
}

@media (min-width: 992px) {
    .card-poster_view-default {
        height: 50vh;
    }
    .card-poster_view-max-content {
        height: 40vh;
    }
}

@media (min-width: 1200px) {
    .card-poster_view-default {
        height: 50vh;
    }
    .card-poster_view-max-content {
        height: 30vh;
    }
}
</style>