import { createStore } from 'vuex'
const axios = require('axios')

export default createStore({
  state: {
    user: {
      auth: false,
      uid: null,
      name: '',
      surname: '',
      jwt: ''
    }
  },
  getters: {
    IS_AUTH: state => {
      return state.user.auth
    },
    USER_TOKEN: state => {
      return state.user.jwt
    },
    USER: state => {
      return {
        auth: true,
        uid: state.user.uid,
        name: state.user.name,
        surname: state.user.surname,
        jwt: state.user.jwt,
      }
    }
  },
  mutations: {
    LOGIN: (state, payload) => {
      localStorage.setItem('jwt', payload.jwt)
      state.user.auth = true
      state.user.uid = payload.uid
      state.user.name = payload.name
      state.user.surname = payload.surname
      state.user.jwt = payload.jwt
      
    },
    LOGOUT: (state, payload) => {
      localStorage.removeItem('jwt');
      if (payload == state.user.uid)
      state.user = {
        auth: false,
        uid: null,
        name: '',
        surname: '',
        jwt: ''
      }
    }
  },
  actions: {
    COLD_LOGIN: (context, payload) => {
      let jwt = payload.split('.')
      let user = JSON.parse(atob(jwt[1]))
      let auth_data = {
        uid: user.uid,
        name: user.name,
        surname: user.surname,
        jwt: payload,
      }

      context.commit('LOGIN', auth_data)
    },
    LOGIN: (context, payload) => {
      axios
        .get(`https://iny.su/api.php?_action=auth.jwt&v=0.1&login=${payload.login}&password=${payload.password}`)
        .then(res => {
            let data = res.data

            if (data.auth) {
              let jwt = data.jwt.split('.')
              let user = JSON.parse(atob(jwt[1]))
              let auth_data = {
                uid: user.uid,
                name: user.name,
                surname: user.surname,
                jwt: data.jwt,
              }

              context.commit('LOGIN', auth_data)
            }
        })

    }
  },
  modules: {

  }
})
