<template>
    <nav class="navbar navbar-expand-lg navbar-dark theme-header sticky-top py-2">
        <div class="container">
            <b class="navbar-brand ps-lg-5">{{ logo }}</b>
            <button class="navbar-toggler" type="button" data-mdb-toggle="collapse" data-mdb-target="#navbarMainMenu">
                <i class="fas fa-bars"></i>
            </button>
            <div class="collapse navbar-collapse" id="navbarMainMenu">
                <ul class="navbar-nav  me-lg-auto mb-lg-0">
                    <li class="nav-item">
                        <router-link class="nav-link theme-link" to="/">Главная</router-link>
                    </li>
                    <li class="nav-item">
                        <router-link class="nav-link theme-link" to="/search">Поиск</router-link>
                    </li>
                    <li class="nav-item">
                        <router-link class="nav-link theme-link" to="/top10">В тренде</router-link>
                    </li>
                </ul>
                <ul class="navbar-nav  ms-lg-auto text-lg-end mb-lg-0 pe-lg-5">
                    <li v-if="is_auth" class="nav-item">
                        <router-link class="nav-link theme-link" to="/subscriptions">Подписки</router-link>
                    </li>
                    <li class="nav-item">
                        <router-link class="nav-link theme-link" to="/auth">{{menu_auth()}}</router-link>
                    </li>
                </ul>
            </div>
        </div>
    </nav>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
    name: "AppHeader",
    props: {
        logo: String
    },
    data() {
        return {
            is_auth: this.$store.getters.IS_AUTH
        }
    },
    computed: {
        ...mapGetters(['IS_AUTH'])
    },
    methods: {
        menu_auth() {
            if (this.is_auth) {
                return 'Аккаунт'
            } else {
                return 'Войти'
            }
        }
    },
    watch: {
        IS_AUTH(status) {
            this.is_auth = status
        }
    }
};
</script>

<style scoped>
.navbar {
    backdrop-filter: blur(12px) brightness(0.7);
    border-bottom: 1px solid #717170;
}
</style>